body{
  background-color:olivedrab;
}
.App{
background-color:#3f3160;
min-height: 500px;
width:400px;
margin:40px auto;
border:17px solid white
}
#to-do-form input{
  background-color: rgb(27,112,137);
  border:0;
  width:200px;
  height:50px;
  padding:0 25px;
  margin:20px;
  font-size: 18px;
  border-radius: 10px;
  color:white;
}
#to-do-form input::placeholder{
  color:rgb(255,255,255,0.5)
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height:50px;
  width:80px;
  border-radius:5px;
  border:0px;
  background-color: rgb(202,156,24);
  cursor: pointer;
  font-weight: bolder;
  color:white;
}
#to-do-form button:focus{
  outline: none;
}
#to-do-form h2{
  color: white;
    font-size: 22px;
    font-family: sans-serif;
    margin-left: 88px;
}